

import {
    all,
    not
} from "helpers";

import {default as isBoolean} from "is.boolean";
import {default as isNumber} from "is.number";
import {default as isRegExp} from "is.regexp";
import {default as isSameType} from "is.sameType";
import {default as isString} from "is.string";

export default function isEqual(obj1, obj2) {
    if (not(isSameType(obj1, obj2))) {
        return false;
    }

    if (all(isNumber(obj1, obj2))) {
        return obj1 === obj2 && 1 / obj1 === 1 / obj2;
    }

    if (all(isString(obj1, obj2)) || all(isRegExp(obj1, obj2))) {
        return "" + obj1 === "" + obj2;
    }

    if (all(isBoolean(obj1, obj2))) {
        return obj1 === obj2;
    }

    return false;
}


