import {
    arraySlice,
    hasOwnProperty,
    toString
} from "./utils";

import {
    all,
    any,
    not
} from "./helpers";

function setInterfaces(options) {
    for (var option in options) {
        if (hasOwnProperty.call(options, option) && isFunction(options[option])) {
            let interfaces = options[option].api || defaultApi,
                i = 0,
                len = interfaces.length;

            for (; i < len; i++) {
                if (interfaces[i] === "all") {
                    options.all[option] = all(options[option]);
                }

                if (interfaces[i] === "any") {
                    options.any[option] = any(options[option]);
                }

                if (interfaces[i] === "not") {
                    options.not[option] = not(options[option]);
                }
            }
        }
    }
}

const defaultApi = [
    "all",
    "any",
    "not"
];

let is = {
    all:{},
    any:{},
    not:{},
    version: "1.0.0"
};

// Type checks
/* -------------------------------------------------------------------------- */

import {default as isArguments} from "is.arguments";
import {default as isArray} from "is.array";
import {default as isBoolean} from "is.boolean";
import {default as isChar} from "is.char";
import {default as isDate} from "is.date";
import {default as isError} from "is.error";
import {default as isFunction} from "is.function";
import {default as isJson} from "is.json";
import {default as isNaN} from "is.nan";
import {default as isNull} from "is.null";
import {default as isNumber} from "is.number";
import {default as isObject} from "is.object";
import {default as isRegExp} from "is.regexp";
import {default as isSameType} from "is.sameType";
import {default as isString} from "is.string";
import {default as isSymbol} from "is.symbol";
import {default as isUndefined} from "is.undefined";

is.arguments = isArguments;
is.array = isArray;
is.boolean = isBoolean;
is.char = isChar;
is.date = isDate;
is.error = isError;
is.function = isFunction;
is.nan = isNaN;
is.null = isNull;
is.number = isNumber;
is.object = isObject;
is.json = isJson;
is.regexp = isRegExp;
is.sameType = isSameType;
is.string = isString;
is.symbol = isSymbol;
is.undefined = isUndefined;

// api restrictions
is.sameType.api = ["not"];

// Presence checks
/* -------------------------------------------------------------------------- */

import {default as isEmpty} from "is.empty";
import {default as isExisty} from "is.existy";
import {default as isFalsy} from "is.falsy";
import {default as isSpace} from "is.space";
import {default as isTruthy} from "is.truthy";

is.empty = isEmpty;
is.existy = isExisty;
is.falsy = isFalsy;
is.space = isSpace;
is.truthy = isTruthy;

// Arithmetic checks
/* -------------------------------------------------------------------------- */

import {default as isAbove} from "is.above";
import {default as isDecimal} from "is.decimal";
import {default as isEqual} from "is.equal";
import {default as isEven} from "is.even";
import {default as isFinite} from "is.finite";
import {default as isInfinite} from "is.infinite";
import {default as isInteger} from "is.integer";
import {default as isNegative} from "is.negative";
import {default as isOdd} from "is.odd";
import {default as isPositive} from "is.positive";
import {default as isUnder} from "is.under";
import {default as isWithin} from "is.within";

is.above = isAbove;
is.decimal = isDecimal;
is.equal = isEqual;
is.even = isEven;
is.finite = isFinite;
is.infinite = isInfinite;
is.integer = isInteger;
is.negative = isNegative;
is.odd = isOdd;
is.positive = isPositive;
is.under = isUnder;
is.within = isWithin;

// api restrictions
is.above.api = ["not"];
is.equal.api = ["not"];
is.under.api = ["not"];
is.within.api = ["not"];

// Regexp checks
/* -------------------------------------------------------------------------- */

import {default as isAffirmative} from "is.affirmative";
import {default as isAlphaNumeric} from "is.alphaNumeric";
import {default as isCaPostalCode} from "is.caPostalCode";
import {default as isCreditCard} from "is.creditCard";
import {default as isDateString} from "is.dateString";
import {default as isEmail} from "is.email";
import {default as isEppPhone} from "is.eppPhone";
import {default as isHexadecimal} from "is.hexadecimal";
import {default as isHexColor} from "is.hexColor";
import {default as isIp} from "is.ip";
import {default as isIpv4} from "is.ipv4";
import {default as isIpv6} from "is.ipv6";
import {default as isNanpPhone} from "is.nanpPhone";
import {default as isSocialSecurityNumber} from "is.socialSecurityNumber";
import {default as isTimeString} from "is.timeString";
import {default as isUkPostalCode} from "is.ukPostalCode";
import {default as isUrl} from "is.url";
import {default as isUsZipCode} from "is.usZipCode";
import {default as setRegexp} from "is.setRegexp";

is.affirmative = isAffirmative;
is.alphaNumeric = isAlphaNumeric;
is.caPostalCode = isCaPostalCode;
is.creditCard = isCreditCard;
is.dateString = isDateString;
is.email = isEmail;
is.eppPhone = isEppPhone;
is.hexadecimal = isHexadecimal;
is.hexColor = isHexColor;
is.ip = isIp;
is.ipv4 = isIpv4;
is.ipv6 = isIpv6;
is.nanpPhone = isNanpPhone;
is.setRegexp = setRegexp.bind(is);
is.socialSecurityNumber = isSocialSecurityNumber;
is.timeString = isTimeString;
is.ukPostalCode = isUkPostalCode;
is.url = isUrl;
is.usZipCode = isUsZipCode;

// String checks
/* -------------------------------------------------------------------------- */

import {default as isCapitalized} from "is.capitalized";
import {default as isEndWith} from "is.endWith";
import {default as isInclude} from "is.include";
import {default as isLowerCase} from "is.lowercase";
import {default as isPalindrome} from "is.palindrome";
import {default as isStartWith} from "is.startWith";
import {default as isUppercase} from "is.uppercase";

is.capitalized = isCapitalized;
is.endWith = isEndWith;
is.include = isInclude;
is.lowercase = isLowerCase;
is.palindrome = isPalindrome;
is.startWith = isStartWith;
is.uppercase = isUppercase;

// api restrictions
is.endWith.api = ["not"];
is.include.api = ["not"];
is.startWith.api = ["not"];

// String checks
/* -------------------------------------------------------------------------- */

// Object checks
/* -------------------------------------------------------------------------- */

import {default as isDomNode} from "is.domNode";
import {default as isPropertyCount} from "is.propertyCount";
import {default as isPropertyDefined} from "is.propertyDefined";
import {default as isWindowObject} from "is.windowObject";

is.domNode = isDomNode;
is.propertyCount = isPropertyCount;
is.propertyDefined = isPropertyDefined;
is.windowObject = isWindowObject;

// api restrictions
is.propertyCount.api = ["not"];
is.propertyDefined.api = ["not"];

// Array checks
/* -------------------------------------------------------------------------- */

import {default as isInArray} from "is.inArray";
import {default as isSorted} from "is.sorted";

is.inArray = isInArray;
is.sorted = isSorted;

// api restrictions
is.inArray.api = ["not"];

// Date checks
/* -------------------------------------------------------------------------- */

import {default as isToday} from "is.today";

is.today = isToday;

// lots more to do

// Environment checks
/* -------------------------------------------------------------------------- */

import {default as isBrowser} from "is.browser";
import {default as isChrome} from "is.chrome";
import {default as isFirefox} from "is.firefox";
import {default as isNode} from "is.node";

is.browser = isBrowser;
is.chrome = isChrome;
is.firefox = isFirefox;
is.node = isNode;

// api restrictions
is.browser.api = ["not"];
is.chrome.api = ["not"];
is.firefox.api = ["not"];
is.ie.api = ["not"];
is.node.api = ["not"];

setInterfaces(is);

// lots more to do

export default is;


