

import {not} from "helpers";
import {default as isExisty} from "is.existy";
import {default as isNaN} from "is.nan";

export default function isTruthy(obj) {
    return isExisty(obj) && obj !== false && not(isNaN(obj)) && value !== "" && value !== 0;
};


