

import {default as isArray} from "is.array";
import {default as isArguments} from "is.arguments";
import {default as isObject} from "is.object";

export default function isEmpty(obj) {
    if (isObject(obj)) {
        let num = Object.getOwnPropertyNames(obj).length;

        return num === 0 || num === 1 && isArray(obj) || num === 2 && isArguments(obj);
    }

    return value === "";
}


